<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>{{title}} Address Information</h1>
      <div class="chartWrap">
        <a class="iconfont cart" @click="$router.push('cart')"> &#xe669; </a>
      </div>
    </section>
    <div class="inputWrap">
      <div class="coat">
        <div class="label">First Name<font>*</font></div>
        <van-field v-model="form.firstName" />
      </div>

      <div class="coat">
        <div class="label">Last Name<font>*</font></div>
        <van-field v-model="form.lastName" />
      </div>

      <div class="country">
        <div class="coat_left">
          <div class="label">Country</div>
          <van-field
            @click="showPicker(0)"
            :readonly="true"
            :required="true"
            right-icon="arrow-down"
            @click-right-icon='showPicker(0)'
            v-model="form.countryName"
          />
        </div>
        <div class="coat_right">
          <div class="label">City</div>
          <van-field
            @click="showPicker(1)"
            @click-right-icon='showPicker(1)'
             :readonly="true"
            :required="true"
            right-icon="arrow-down"
            v-model="form.provinceName"
          />
        </div>
      </div>

      <div class="region">
        <div class="label">Region</div>
        <van-field
          @click="showPicker(2)"
          @click-right-icon='showPicker(2)'
          :readonly="true"
          :required="true"
          right-icon="arrow-down"
          v-model="form.cityName"
        />
      </div>

      <div class="coat">
        <div class="label">Street and Building</div>
        <van-field
          :required="true"
          v-model="form.street"
        />
      </div>

      <div class="coat">
        <div class="label">Postal Code</div>
        <van-field
          :required="true"
          v-model="form.postalCode" />
      </div>

      <div class="coat">
        <div class="label">Apartment and Floor</div>
        <van-field v-model="form.apartment" />
      </div>
      <div class="coat">
        <div class="label">Nearest Landmark</div>
        <van-field v-model="form.building" />
      </div>
      <div class="comment">
        <p>Address Type</p>
        <div
          :class="[{ red: typeIndex == i }, 'setmeal']"
          v-for="(item, i) in typeList"
          :key="item.name"
          @click="typeIndex = i;form.landmark=i+1;"
        >
          {{ item.name }}
        </div>
        <!-- <div class="setmeal">
                Home
            </div>
                <div class="setmeal">
                Work
            </div>
                <div class="setmeal">
                School
            </div> -->
        <div style="clear: both;"></div>
      </div>
      <div class="coat">
        <div class="codeName">+971</div>
        <div class="label">Mobile Number<font>*</font></div>
        <van-field v-model="form.mobile" />
      </div>

      <div class="coat">
        <div class="codeName">+971</div>
        <div class="label">Landline Number</div>
        <van-field v-model="form.landlineNumber" />
      </div>
      <van-checkbox v-model="checked" checked-color="red"
        >Set as default address</van-checkbox>
      <van-button type="danger" @click="confirm">Save Address</van-button>
    </div>
    <div class="pickerWrap" v-show="showPick" @click="showPick = false"></div>
    <div class="picker">
      <van-picker
        :columns="pickList"
        show-toolbar
        :default-index="pickerIdx"
        ref="picker"
        @change="onChange"
        v-show="showPick"
        @cancel="onCancel"
        @confirm="onConfirm"
      />
    </div>
  </div>
</template>

<script>
import { addAddress, deleteAddress,editAddress,getAddress,country, city } from "@/api/address";
import {validateTel} from "@/utils/validate";
export default {
  name: "addAddress",
  data() {
    return {
      countryList: [],
      countryIdx:0,
      cityList: [],
      cityIdx:0,
      pickList: [],
      pickerIdx:0,
      regionList: [],
      regionIdx:0,
      address:{},
      level: 0,
      showPick: false,
      value: "",
      checked: false,
      typeIndex: 0,
      title:"Add",
      id:this.$route.query.id,
      typeList: [
        { name: "None", bol: true },
        { name: "Home", bol: false },
        { name: "Work", bol: false },
        { name: "School", bol: false }
      ],
      form: {
        //联系人姓
        firstName: "",
        //联系人名
        lastName: "",
        // 国家
        countryName: "",
        // 国家编号
        countryCode: "",
        //城市编号
        provinceCode: "",
        //省名
        provinceName: "",
        //城市编号
        cityCode: "",
        //城市名
        cityName: "",
        //街道
        street: "",
        //建筑
        building: "",
        //手机号
        mobile: "",
        //房间号
        apartment: "",
        //地址标识
        landmark: 1,
        //邮政编码
        postalCode:'',
      }
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if(this.id){

          getAddress({id:this.id}).then(res=>{
                this.address=res.data.result;
                this.form=this.address;
                this.typeIndex=this.form.landmark-1;
                this.initads("edit")
          });
          this.title='Edit';
      }else {
        this.initads("add")
      }

    },
    initads(pageType){
      country().then(res => {
        this.countryList = res.data.result;
        if(pageType=='edit'){
          this.countryList.map((value,idx)=>{
              if(value.countryCode==this.form.countryCode){
                this.countryIdx=idx;
              }
          })
        }
        this.form.countryCode = this.countryList[this.countryIdx].countryCode;
        this.form.countryName = this.countryList[this.countryIdx].countryName;
        city({ countryCode: this.form.countryCode, parentCode: 0 }).then(
          res => {
            this.cityList = res.data.result;
            if(pageType=='edit'){
              this.cityList.map((value,idx)=>{
                if(value.regionCode==this.form.provinceCode){
                  this.cityIdx=idx;
                }
              })
            }

            if(!!this.form.provinceCode){

              city({ countryCode: this.form.provinceCode, parentCode: this.form.countryCode }).then(
                res => {
                  this.regionList = res.data.result;
                  if(pageType=='edit'){
                    this.regionList.map((value,idx)=>{
                      if(value.regionCode==this.form.cityCode){
                        this.regionIdx=idx;
                      }
                    })
                  }
                }
              );

            }

          }
        );
      });
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    confirm() {

      if(!this.validateForm()){
        return false;
      }
      let func=this.form.id?editAddress:addAddress;
      this.form.addressType=this.checked?1:0;
      func(this.form).then(res => {
        this.$toast('Save address successfully')
        this.$router.back(-1);
      });

      // this.$validator.validateAll().then(result => {
      //   if (result) {
      //   }
      // });

    },
    validateForm(){
      if(!this.form.firstName){
        this.$toast("FirstName cannot be empty");
        return false;
      }
      if(!this.form.lastName){
        this.$toast("LastName cannot be empty");
        return false;
      }
      if(!this.form.countryName){
        this.$toast("CountryName cannot be empty");
        return false;
      }
      if(!this.form.provinceName){
        this.$toast("Region cannot be empty");
        return false;
      }
      if(!this.form.cityName){
        this.$toast("City cannot be empty");
        return false;
      }
      if(!this.form.street){
        this.$toast("Street cannot be empty");
        return false;
      }
      if(!this.form.postalCode){
        this.$toast("PostalCode cannot be empty");
        return false;
      }
      if(!this.form.mobile){
        this.$toast("Mobile cannot be empty");
        return false;
      }else{
        if(!validateTel(this.form.mobile)){
          this.$toast("telephone number format is 9 digits");
          return false;
        }
      }
      return true;
    },
    onChange() {

    },
    showPicker(level) {
      this.level = level;
      switch (level) {
        case 0:
          this.pickList = this.countryList.map(c => {
            return c.countryName;
          });
          this.pickerIdx=this.countryIdx;
          break;
        case 1:
          this.pickList = this.cityList.map(c => {
            return c.regionName;
          });
          this.pickerIdx=this.cityIdx;
          break;
        case 2:
          this.pickList = this.regionList.map(c => {
            return c.regionName;
          });
          this.pickerIdx=this.regionIdx;
          break;
      }

      this.showPick = true;
    },
    onCancel() {
      this.showPick = false;
    },
    onConfirm(name, index) {
      this.showPick = false;
      switch (this.level) {
        case 0:
          this.form.countryCode = this.countryList[index].countryCode;
          this.form.countryName = this.countryList[index].countryName;
          this.countryIdx=index;
          city({
            countryCode: this.form.countryCode,
            parentCode: 0
          }).then(res => {
            this.cityList = res.data.result;
          });
          break;
        case 1:
          this.form.provinceCode = this.cityList[index].regionCode;
          this.form.provinceName = name;
          this.cityIdx=index;
          city({
            countryCode: this.form.countryCode,
            parentCode: this.form.provinceCode
          }).then(res => {
            this.regionList = res.data.result;
          });
          break;
        case 2:
          this.form.cityCode = this.regionList[index].regionCode;
          this.form.cityName = name;
          this.regionIdx=index;
          break;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.head {
  padding: 0 15px;
  border-bottom:solid 1px #e5e5e5;
}
.inputWrap {
  width: 100%;
  padding-left: 4%;
  border-top:solid 10px #f6f6f6;
  /deep/ .van-field__control{ padding-left: 10px;}
  .coat {
    width: 100%;
    display: -webkit-box;
    border-bottom: #f6f6f6 1px solid;
    height: 50px;
    .label {
      color: #999999;
      font-size: 14px;
    }
    .codeName {
      display: flex;
      align-items: center;
      padding-right: 25px;
      font-size: 14px;
    }
    .van-cell {
      padding: 10px 0 10px 15px;
      width: 202px;
    }
  }
  font {
    color: red;
  }
}
.label {
  display: flex;
  align-items: center;
}
.comment {
  width: 100%;
  padding-bottom: 15px;
  border-bottom: #f6f6f6 1px solid;
  p {
    padding-top: 14px;
  }
}
.setmeal {
  width: 61px;
  height: 27px;
  font-size: 14px;
  line-height: 25px;
  border: #666666 1px solid;
  border-radius: 2px;
  text-align: center;
  color: #666666;
  float: left;
  margin-top: 11px;
  margin-right: 15px;
}
.red {
  border: #e33946 1px solid;
  color: #e33946;
}
.region {
  width: 100%;
  display: -webkit-box;
  border-bottom: #f6f6f6 1px solid;
  .label {
    color: #999999;
  }
  .van-cell {
    padding: 10px 0 10px 15px;
    width: 282px;
  }
}
.country {
  width: 100%;
  color: #999999;
  display: flex;
  justify-content: space-between;
  .coat_left {
    width: 48%;
    display: -webkit-box;
    border-bottom: #f6f6f6 1px solid;
    .van-cell {
      padding: 10px 15px;
      width: 125px;
      .van-field__clear,
      .van-field__right-icon {
        margin: 0;
      }
    }
  }
  .coat_right {
    width: 48%;
    display: -webkit-box;
    border-bottom: #f6f6f6 1px solid;
    .van-cell {
      padding: 10px 10px 10px 15px;
      width: 125px;
    }
  }
}
.van-checkbox {
  padding-top: 10px;
  font-size: 12px;
  color: #666666;
}
.van-button {
  width: 96%;
  margin: 28px 4% 67px 0;
}
.picker {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 120;
}
.pickerWrap {
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 100;
  position: fixed;
  top: 0;
}
</style>
<style lang="scss">
.inputWrap {
  .van-checkbox__label {
    color: #999999;
  }
}
</style>



